import React, {PropTypes} from 'react';
import {Link} from 'react-router';

import styles from './styles.scss';

class List extends React.Component {

  static propTypes = {
    entries: PropTypes.arrayOf(PropTypes.shape({
      name: PropTypes.string,
      href: PropTypes.string
    })).isRequired
  }

  insertDividers(items) {
    const entries = [];

    items.forEach(function(item, index) {
      entries.push(
        <li key={entries.length}>
          <Link to={item.href} className={styles.entry}>{item.name}</Link>
        </li>
      );

      if (index < items.length - 1) {
        entries.push(<li key={entries.length} className={styles.divider}></li>);
      }
    });

    return entries;
  }

  render() {
    const entries = this.insertDividers(this.props.entries);

    return (
      <ul className={styles.entries}>
        {entries}
      </ul>
    );
  }

}

export default List;



